<template>
  <div class="orderDetails-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/personal/myorder/allOrder' }">我的订单</el-breadcrumb-item>
      <el-breadcrumb-item>订单详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="consignee">
      <p>收货人信息</p>
      <ul>
        <li v-for="(item, index) in consigneeList" :key="index">
          <div class="item-d" :class="item.isDefault ? 'active' : ''" @click="switchConsignee(item.id)">
            <p>
              {{item.name}}
              <span>
                {{item.phone}}
              </span>
            </p>
            <p>
              {{item.address + ' ' + item.detailedAddress}}
            </p>
          </div>
        </li>
      </ul>
      <el-button size="small" @click="showAddress('1')">
        修改地址
        <i class="el-icon-edit el-icon--right"></i>
      </el-button>
      <el-button size="small" @click="showAddress('2')">
        新增地址
        <i class="el-icon-plus el-icon--right"></i>
      </el-button>
    </div>
    <div class="list">
      <p>送货清单</p>
      <el-table
        ref="multipleTable1"
        :data="paperbookData"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column
          label="商品信息"
          align="left">
          <template slot-scope="scope">
            <div class="goods-view">
              <div class="left">
                <img :src="scope.row.imgUrl" alt="">
              </div>
              <div class="right">
                <p class="p-describe">
                  {{scope.row.describe}}
                </p>
                <p class="p-author">
                  {{scope.row.author}}
                </p>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="money"
          label="单价（元）"
          align="center"
          width="300">
          <template slot-scope="scope">
            <span>
              ￥{{scope.row.money}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="num"
          label="数量"
          align="center"
          width="300">
          <template slot-scope="scope">
            <span>
              {{scope.row.num}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="money"
          label="总价（元）"
          align="center"
          width="150">
          <template slot-scope="scope">
            <span class="rowMoney">
              ￥{{scope.row.money}}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 操作收货地址 -->
    <el-dialog :title="addressTitle + '收货地址'" :visible.sync="dialogFormVisible" center width="500px" @close="closeDialog">
      <el-form :model="consigneeForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="收货人" prop="name">
          <el-input v-model="consigneeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="consigneeForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="所在地区" prop="address">
          <el-cascader
            separator=" "
            :options="options"
            v-model="consigneeForm.address"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
        <el-form-item label="详细地址" prop="detailedAddress">
          <el-input type="textarea" v-model="consigneeForm.detailedAddress"></el-input>
        </el-form-item>
        <el-form-item label="邮编" prop="zipCode">
          <el-input v-model="consigneeForm.zipCode"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="confirmClick(consigneeForm.id)">
          确认{{addressTitle}}地址
        </el-button>
        <el-button>
          返回购物车查看商品
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: '',
  data () {
    var checkName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('姓名不能为空！'))
      } else {
        callback()
      }
    }
    var checkPhone = (rule, value, callback) => {
      var reg = /^1[34578]\d{9}$/
      if (value === '') {
        callback(new Error('手机号不能为空！'))
      } else if (!reg.test(value)) {
        callback(new Error('请输入正确的手机号！'))
      } else {
        callback()
      }
    }
    var checkAddress = (rule, value, callback) => {
      if (value.length === 0) {
        callback(new Error('所在地区不能为空！'))
      } else {
        callback()
      }
    }
    var checkZipCode = (rule, value, callback) => {
      if (!value) {
        callback(new Error('邮编不能为空！'))
      } else {
        callback()
      }
    }
    var checkDetailedAddress = (rule, value, callback) => {
      if (!value) {
        callback(new Error('详细地址不能为空！'))
      } else {
        callback()
      }
    }
    return {
      dialogFormVisible: false,
      addressTitle: '',
      paperbookData: [{
        id: '1',
        imgUrl: require('@/assets/images/2.png'),
        name: '把话说到心窝里',
        money: '29.90',
        num: 2,
        author: '王阳明',
        describe: '你看萨模块'
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '把话说到心窝里',
        money: '29.90',
        author: '王阳明',
        num: 1,
        describe: '13213131'
      }, {
        id: '3',
        imgUrl: require('@/assets/images/2.png'),
        name: '把话说到心窝里',
        money: '29.90',
        author: '王阳明',
        num: 1,
        describe: '你看大家撒的撒而爱的撒旦撒大菩萨模块'
      }],
      multipleSelection1: [],
      consigneeForm: {
        id: '',
        name: '',
        phone: '',
        address: [],
        detailedAddress: '',
        zipCode: ''
      },
      rules: {
        name: [
          { required: true, validator: checkName, trigger: 'blur' }
        ],
        phone: [
          { required: true, validator: checkPhone, trigger: 'blur' }
        ],
        address: [
          { required: true, validator: checkAddress, trigger: 'change' }
        ],
        detailedAddress: [
          { required: true, validator: checkDetailedAddress, trigger: 'blur' }
        ],
        zipCode: [
          { required: true, validator: checkZipCode, trigger: 'blur' }
        ]
      },
      formLabelWidth: '120px',
      consigneeList: [{
        id: '1',
        isDefault: true,
        name: 'lianchao',
        phone: '18090073512',
        address: '四川省 成都市 彭山县',
        detailedAddress: '建设路111111号建设',
        zipCode: '123'
      }, {
        id: '2',
        isDefault: false,
        name: 'lianchao',
        phone: '18090073512',
        address: '四川省 成都市 彭山县',
        detailedAddress: '建设路111111号',
        zipCode: '1234'
      }, {
        id: '3',
        isDefault: false,
        name: 'lianchao',
        phone: '18090073512',
        address: '四川省 成都市 彭山县',
        detailedAddress: '建设路111111号',
        zipCode: '12345'
      }],
      options: [{
        value: 'zhinan',
        label: '指南',
        children: [{
          value: 'shejiyuanze',
          label: '设计原则',
          children: [{
            value: 'yizhi',
            label: '一致'
          }, {
            value: 'fankui',
            label: '反馈'
          }, {
            value: 'xiaolv',
            label: '效率'
          }, {
            value: 'kekong',
            label: '可控'
          }]
        }, {
          value: 'daohang',
          label: '导航',
          children: [{
            value: 'cexiangdaohang',
            label: '侧向导航'
          }, {
            value: 'dingbudaohang',
            label: '顶部导航'
          }]
        }]
      }, {
        value: 'zujian',
        label: '组件',
        children: [{
          value: 'basic',
          label: 'Basic',
          children: [{
            value: 'layout',
            label: 'Layout 布局'
          }, {
            value: 'color',
            label: 'Color 色彩'
          }]
        }, {
          value: 'form',
          label: 'Form',
          children: [{
            value: 'radio',
            label: 'Radio 单选框'
          }, {
            value: 'checkbox',
            label: 'Checkbox 多选框'
          }, {
            value: 'input',
            label: 'Input 输入框'
          }]
        }, {
          value: 'data',
          label: 'Data',
          children: [{
            value: 'table',
            label: 'Table 表格'
          }, {
            value: 'tag',
            label: 'Tag 标签'
          }]
        }, {
          value: 'notice',
          label: 'Notice',
          children: [{
            value: 'alert',
            label: 'Alert 警告'
          }, {
            value: 'loading',
            label: 'Loading 加载'
          }]
        }, {
          value: 'navigation',
          label: 'Navigation',
          children: [{
            value: 'menu',
            label: 'NavMenu 导航菜单'
          }, {
            value: 'tabs',
            label: 'Tabs 标签页'
          }]
        }, {
          value: 'others',
          label: 'Others',
          children: [{
            value: 'dialog',
            label: 'Dialog 对话框'
          }, {
            value: 'tooltip',
            label: 'Tooltip 文字提示'
          }]
        }]
      }, {
        value: 'ziyuan',
        label: '资源',
        children: [{
          value: 'axure',
          label: 'Axure Components'
        }, {
          value: 'sketch',
          label: 'Sketch Templates'
        }]
      }]
    }
  },
  methods: {
    switchConsignee (id) { // 切换收货人
      this.consigneeList.forEach(e => {
        e.isDefault = e.id === id
      })
    },
    showAddress (type) { // 显示地址弹出框
      this.dialogFormVisible = true
      if (type === '1') {
        this.addressTitle = '修改'
        this.consigneeList.forEach(e => {
          if (e.isDefault) {
            this.consigneeForm.id = e.id
            this.consigneeForm = {
              id: e.id,
              name: e.name,
              phone: e.phone,
              address: [],
              detailedAddress: e.detailedAddress,
              zipCode: e.zipCode
            }
          }
        })
      } else {
        this.addressTitle = '新增'
        this.consigneeForm = {
          id: '',
          name: '',
          phone: '',
          address: [],
          detailedAddress: '',
          zipCode: ''
        }
      }
    },
    handleChange (value) {
      console.log(value)
    },
    confirmClick (id) { // 收货地址提交
      // console.log(this.consigneeForm)
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    closeDialog () { // 收货地址弹出框 关闭时清空表单
      this.$refs['ruleForm'].resetFields()
    },
    getUserAddress () { // 获取用户收货地址
      // this.$get(this.$allUrl.getUserAddress + `?userId=${userId}`)
    }
  }
}
</script>
<style lang="less" scoped>
  .orderDetails-view{
    padding: 30px 10px 0 10px;
    .consignee{
      margin-top: 30px;
      ul{
        list-style: none;
        padding: 0;
        margin: 0;
        li{
          margin-bottom: 20px;
          .item-d{
            p{
              padding: 5px 0;
              margin: 0;
            }
            border: 1px solid #666666;
            cursor: pointer;
            display: inline-block;
            padding: 0 10px;
          }
          .active{
            border: 1px solid red;
          }
        }
      }
    }
    .list{
      .goods-view{
        display: flex;
        .left{
          img{
            width: 60px;
            height: 80px;
          }
        }
        .right{
          flex: 1;
          padding-left: 30px;
          p{
            padding: 0;
            margin: 0;
          }
          .p-describe{
            color: #000000;
            overflow: hidden;
            word-break: break-all;
            display: -moz-box;
            -moz-box-orient:vertical;
            display: -webkit-box;
            -webkit-box-orient:vertical;
            text-overflow: ellipsis;
            -webkit-line-clamp: 2;
          }
          .p-author{
            margin-top: 10px;
            color: #666666;
          }
        }
      }
      .number-view{
        text-align: center;
        span{
          display: inline-block;
          line-height: 30px;
          text-align: center;
          vertical-align: middle;
        }
        .reduce, .plus{
          background: #DCDCDC;
          cursor: pointer;
          font-size: 20px;
          font-weight: 600;
          outline: none;
          border: 0;
          line-height: 30px;
          padding: 0;
          margin: 0;
          vertical-align: middle;
        }
        .num{
          height: 28px;
          padding: 0 10px;
          background: white;
          border: 1px solid #DCDCDC;
          margin: 0 -4px 0 -4px;
        }
      }
      .cz{
        span{
          cursor: pointer;
        }
      }
      .rowMoney{
        font-size: 16px;
        color: red;
      }
    }
  }
</style>



