<template>
  <div class="mybookshelf-view">
    <nav-menu :navMenuList="navMenuList"
    :activeIndex="activeIndex"
    @handleSelect="handleSelect"></nav-menu>
    <div class="search-panel">
      <div class="allPage">
        共100本
      </div>
      <div class="d-search">
        <input type="text" placeholder="请输入书籍名称">
        <button @click="cliclSearch">
          <i class="el-icon-search"></i>
        </button>
      </div>
    </div>
    <ul class="list-view">
      <li v-for="(item,index) in list" :key="index">
        <div class="left">
          <img :src="item.imgUrl" alt="">
        </div>
        <div class="right">
          <strong>{{item.name}}</strong>
          <p class="p-name">{{item.author}}</p>
          <el-rate
            class="star"
            v-model="item.star"
            disabled
            show-score
            text-color="#ff9900"
            score-template="{value}">
          </el-rate>
          <p>
            {{item.time}}购入
          </p>
          <p>
            已阅读{{item.read}}
          </p>
          <button>继续阅读</button>
        </div>
      </li>
    </ul>
    <div class="pagination">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage3"
        :page-size="100"
        layout="prev, pager, next, jumper"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import NavMenu from '@/base/navmenu/navmenu'
export default {
  name: '',
  components: {
    'nav-menu': NavMenu
  },
  data () {
    return {
      currentPage3: 5,
      activeIndex: '0',
      navMenuList: [{
        text: '我的书架',
        pathName: 'MyBookshelf'
      }],
      list: [{
        id: '1',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 5,
        read: '10%'
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2,
        read: '10%'
      }, {
        id: '3',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2.6,
        read: '10%'
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2,
        read: '10%'
      }, {
        id: '3',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2.6,
        read: '10%'
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2,
        read: '10%'
      }, {
        id: '3',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2.6,
        read: '10%'
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2,
        read: '10%'
      }, {
        id: '3',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        time: '2018-5-25',
        star: 2.6,
        read: '10%'
      }]
    }
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    cliclSearch () {
      alert('搜索')
    },
    handleSelect (key) {
      this.activeIndex = key
      // console.log(this.navMenuList[key].text)
    }
  }
}
</script>
<style lang="less" scoped>
  .mybookshelf-view{
    position: relative;
  }
  .pagination{
    text-align: right;
    padding: 50px 0;
  }
  .search-panel{
    height: 30px;
    width: 860px;
    position: absolute;
    right: 0;
    top: -2px;
    display: flex;
    line-height: 30px;
    .allPage{
      flex: 1;
      color: #7B6EA3;
      font-size: 14px;
      font-weight: 600;
    }
    .d-search{
      line-height: 20px;
      button{
        background-color: #B7151E;
        border: 0;
        outline: none;
        cursor: pointer;
        color: white;
        padding: 0 10px;
        height: 100%;
        float: right;
        font-size: 16px;
      }
      input{
        width: 200px;
        outline: none;
        border: 2px solid #B7151E;
        height: 24px;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
      }
    }
  }
  .list-view{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    li{
      max-width: 50%;
      min-width: 50%;
      display: flex;
      padding: 40px 0;
      border-bottom: 1px solid #DBDBDB;
      p{
        padding: 0;
        margin: 0;
        font-size: 14px;
        padding-bottom: 10px;
        color: #9F9F9F;
      }
      .left{
        padding: 0 20px;
        img{
          width: 150px;
          height: 180px;
        }
      }
      .right{
        .star{
          padding-bottom: 10px;
        }
        .p-name{
          padding-top: 10px;
        }
        button{
          width: 100px;
          height: 28px;
          background-color: #B7151E;
          color: white;
          outline: none;
          border: 0;
          border-radius: 6px;
          cursor: pointer;
        }
      }
    }
  }
</style>
<style>
.el-pagination.is-background .el-pager li:not(.disabled).active{
    background: #F76B6E !important;
  }
</style>



