              font-size: 14px;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
              padding: 5px 0;
              font-weight: 600;
            }
            .p2{
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            img{
              width: 100%;
              height: 150px;
            }
          }
        }
