          font-size: 14px;
          color: #7C7C7C;
          width: 100%;
          li{
            width: 100%;
            padding: 8px 0;
            border-top: 1px solid #eeeeee;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            cursor: pointer;
          }
          li:hover{
            color: #A90013;
          }
        }
