<template>
  <div class="allOrder-view">
    <el-checkbox v-model="checked">全选</el-checkbox>
    <list-data
    :dataList="dataList"
    @operation="operation"
    @handleCurrentChange="handleCurrentChange"
    ></list-data>
  </div>
</template>
<script>
import List from './list'
export default {
  name: '',
  components: {
    'list-data': List
  },
  data () {
    return {
      checked: true,
      dataList: [{
        id: '1',
        orderNum: '1496134313',
        time: '2018-5-28 10:10:25',
        imgUrl: require('@/assets/images/2.png'),
        name: '远见网上支付网上支付网上支付网上支付网上支付',
        money: '50.25',
        num: 20,
        author: '孔明',
        state: '4',
        stateText: '待收货',
        payType: 'wx'
      }, {
        id: '2',
        orderNum: '1496134313',
        time: '2018-5-28 10:10:25',
        imgUrl: require('@/assets/images/2.png'),
        name: '远见',
        money: '50.25',
        num: 20,
        author: '孔明',
        state: '4',
        stateText: '待收货',
        payType: 'zfb'
      }, {
        id: '3',
        orderNum: '1496134313',
        time: '2018-5-28 10:10:25',
        imgUrl: require('@/assets/images/2.png'),
        name: '远见',
        money: '50.25',
        num: 20,
        author: '孔明',
        state: '4',
        stateText: '待收货',
        payType: 'zfb'
      }]
    }
  },
  methods: {
    operation (par) {
      const type = par.type
      // const id = par.id
      switch (type) {
        case 'pay':
          alert('zf')
          break
        case 'details':
          alert('详情')
          break
        case 'cancel':
          alert('取消')
          break
        case 'track':
          alert('跟踪')
          break
        default:
          break
      }
    },
    handleCurrentChange (page) {
      console.log(page)
    }
  }
}
</script>
<style lang="less" scoped>
  .allOrder-view{
    margin-top: 20px;
    margin-bottom: 50px;
    .allOrder-data{
      list-style: none;
    }
    .mergePay{
      float: right;
      border: 0;
      padding: 5px 20px;
      background-color: white;
      outline: none;
      cursor: pointer;
      background-color: #b60e0e;
        color: white;
    }
  }
</style>



